<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />

    <section class="van-doc-demo-section">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

        <div class="list_title">
          <span>Tenor</span>
          <span>Bid/Vol.Bid</span>
          <span>Ofr/Vol.Ofr</span>
        </div>

        <van-collapse v-model="activeNames">
          <van-collapse-item :title="item.title" :name="index" v-for="(item,index) in list">
            {{item.value}}
          </van-collapse-item>
        </van-collapse>

      </van-pull-refresh>
    </section>


  </div>
</template>

<script>
  export default {
    name: "interestRate",
    data() {
      return {
        title: '利率互换',
        isLoading: false,
        activeNames: [0, 1, 2, 3, 4],
        list: [
          {title: 'IRS-FR007', value: '测试测试测试'},
          {title: 'IRS-FR007', value: '测试测试测试'},
          {title: 'IRS-FR007', value: '测试测试测试'},
          {title: 'IRS-FR007', value: '测试测试测试'},
          {title: 'IRS-FR007', value: '测试测试测试'},
        ]
      };
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      },
      onRefresh() {
        setTimeout(() => {
          this.$toast('刷新成功');
          this.isLoading = false;
          this.count++;
        }, 500);
      }
    }
  }
</script>

<style scoped>

  .list_title {
    height: 40px;
    background: rgba(255, 255, 255, 1);
    margin-bottom: 10px;
  }
  .list_title span {
    font-size: 15.5px;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: rgba(117, 117, 117, 1);
    line-height: 40px;
    float: left;
  }
  .list_title span:nth-child(1){margin-left: 38px;}
  .list_title span:nth-child(2){margin-left: 47.5px;}
  .list_title span:nth-child(3){margin-left: 62px;}

</style>



